<template>
  <div>
    <h3 class="mb-4">Change object data</h3>
    <form @submit.prevent>
      <div class="mb-3">
        <input
          type="text"
          class="form-control"
          placeholder="Name"
          disabled
          v-model="oneObject.name"
        />
      </div>

      <div class="mb-3">
        <input
          type="text"
          class="form-control"
          placeholder="Value..."
          v-model="oneObject.value"
        />
      </div>
      <div class="d-flex justify-content-center">
        <button class="save btn" @click="$emit('edit', oneObject.value)">
          Save
        </button>
        <!-- <button class="cancel btn" @click="$emit('cancel', oneObject.value)">
          Cancel
        </button> -->
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "ObjectData",
  props: {
    oneObject: { type: Object, required: true },
  },
};
</script>

