<template>
  <div class="modal-window" v-if="show === true" @click="hideModalWindow">
    <div class="modal-window-content" @click.stop>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModalWindow",
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    hideModalWindow() {
      this.$emit("hide", false);
    },
  },
};
</script>
<style scoped lang="scss">
@import "../../main.scss";
.modal-window {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(0, 0, 0, 0.4);
  z-index: 1;
}
.modal-window-content {
  background: $white;
  border-radius: 1rem;
  padding: 2rem;
}
</style>
