<template>
  <div>
    <ModalWindow :show="modalWindowVisible" @hide="modalWindowVisible = false">
      <ObjectData :oneObject="editValue" @edit="changeData" />
      <!-- @cancel="cancelChanges" -->
    </ModalWindow>
    <table
      class="table caption-top table-hover align-middle text-center rounded-2"
    >
      <caption>
        List of objects
      </caption>
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Name</th>
          <th scope="col">Value</th>
          <th scope="col">Actions</th>
        </tr>
      </thead>

      <tbody v-if="manyObjects.length > 0">
        <TransitionGroup name="list">
          <tr v-for="oneObject of manyObjects" :key="oneObject.id">
            <th scope="row">{{ oneObject.id }}</th>
            <td>{{ oneObject.name }}</td>
            <td>{{ oneObject.value }}</td>
            <td class="d-flex justify-content-center gap-2">
              <button
                class="edit btn"
                title="Edit"
                @click="changeEditMode(oneObject)"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="18"
                  height="18"
                  fill="currentColor"
                  class="bi bi-pencil-fill"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M12.854.146a.5.5 0 0 0-.707 0L10.5 1.793 14.207 5.5l1.647-1.646a.5.5 0 0 0 0-.708l-3-3zm.646 6.061L9.793 2.5 3.293 9H3.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.207l6.5-6.5zm-7.468 7.468A.5.5 0 0 1 6 13.5V13h-.5a.5.5 0 0 1-.5-.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.5-.5V10h-.5a.499.499 0 0 1-.175-.032l-.179.178a.5.5 0 0 0-.11.168l-2 5a.5.5 0 0 0 .65.65l5-2a.5.5 0 0 0 .168-.11l.178-.178z"
                  />
                </svg>
              </button>
              <button
                class="delete btn"
                title="Delete"
                @click="deleteObject(oneObject.value)"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="18"
                  height="18"
                  fill="currentColor"
                  class="bi bi-trash3-fill"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5Zm-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5ZM4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06Zm6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528ZM8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5Z"
                  />
                </svg>
              </button>
            </td>
          </tr>
        </TransitionGroup>
      </tbody>
      <div v-else class="w-100">
        <h5 class="text-center text-danger">Table is empty</h5>
      </div>
    </table>
  </div>
</template>

<script>
import ModalWindow from "@/components/UI/ModalWindow.vue";
import ObjectData from "@/components/ObjectData.vue";

export default {
  name: "Object",
  components: {
    ModalWindow,
    ObjectData,
  },
  data() {
    return {
      manyObjects: [
        {
          id: 1,
          name: "foo1",
          value: "bar1",
          isEditing: false,
        },
        {
          id: 2,
          name: "foo2",
          value: "bar2",
          isEditing: false,
        },
        {
          id: 3,
          name: "Example 3",
          value: "text 3",
          isEditing: false,
        },
        {
          id: 4,
          name: "Example 4",
          value: "text 4",
          isEditing: false,
        },
        {
          id: 5,
          name: "Test 5",
          value: "Content 5",
          isEditing: false,
        },
        {
          id: 6,
          name: "Test 6",
          value: "Content 6",
          isEditing: false,
        },
      ],
      modalWindowVisible: false,
      editValue: "",
    };
  },
  methods: {
    changeEditMode(data) {
      this.modalWindowVisible = !this.modalWindowVisible;
      this.isEditing = !this.isEditing;
      console.log(data);

      this.editValue = data;
      this.manyObjects = this.manyObjects.map((objct) => {
        if (objct.value === data) {
          objct.isEditing = !objct.isEditing;
        }
        return objct;
      });
    },
    changeData(data) {
      this.manyObjects = this.manyObjects.map((objct) => {
        if (objct.value === data) {
          data = this.editValue;
          this.modalWindowVisible = !this.modalWindowVisible;
        }
        return objct;
      });
    },
    // cancelChanges(data) {
    //   this.manyObjects = this.manyObjects.map((objct) => {
    //     if (objct.value !== data) {
    //       this.editValue = data;
    //       this.modalWindowVisible = !this.modalWindowVisible;
    //     }
    //     return objct;
    //   });
    // },
    deleteObject(data) {
      this.manyObjects = this.manyObjects.filter((objct) => {
        return objct.value !== data;
      });
    },
  },
};
</script>

